.artworks-title {
    font-size: 1.5em;
    margin: 2em;
}

.category-selector {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 1.5em;
}

.category-selector .category-checkbox-container{
    margin: .5em;
}

.category-selector .category-checkbox-container label, 
.category-selector .category-checkbox-container input {
    cursor: pointer;
}

.category-detail {
    cursor: pointer;
}

.link-back-to-artworks {
    cursor: pointer;
    text-decoration: underline;
}